.sidebar{
	.widget{
		@include shadow-box();
		background-color: $white;
		margin-bottom: 30px;
		.form-control{
			with: 100%;
			padding: 7px 15px;
		}
		h3{
			padding: 13px 15px 12px 15px;
			margin: 0;
			font-size: 24px;
			/* text-transform: uppercase; */
			letter-spacing: 1.25px;
			@include font-family(opensans, light);
			-webkit-font-smoothing: antialiased;
		}

		#loginform{}

		.nav{
			&.nav-sidebar{
				margin: 15px 0 0;
				padding: 0;
				list-style-type: none;
				li{
					time{
						@include meta-text();
						padding: 3px 15px 0;
					}
					a{
						display: block;
						padding: 7px 15px;
						border-bottom: 1px solid $white--smoke;
						color: $gray--light;
						font-size: 14px;
						&:hover{
							background-color: transparent;
							color: $red--google--hover;
						}
					}
					&.current-menu-item{
						a{
							background-color: transparent;
							color: $red--google;
							border-left: 3px solid $red--google;
							@include font-family(opensans, bold);
						}
					}
					&.dropdown{

					}
					&.open{
						 .dropdown-menu{
						 	position: relative;
						 	float: none;
						 	margin-top: 0;
						 	box-shadow: none;
						 	li{
						 		a{
						 			font-size: 12px;
						 		}
						 	}
						 }
					}
				}
			}
		}
		ul{
			margin: 15px 0 0;
			padding: 0;
			list-style-type: none;
			&.related-posts{
				padding: 7px 15px;
				li{
					padding-bottom: 15px;
					time{
						display: block;
						@include meta-text();
					}
					a{
						padding-top: 1px;
						color: $red--google;
						&:hover{
							color: $red--google--hover;
						}
					}
					p{
						padding: 7px 15px;
					}
				}
			}
			&#recentcomments, &.plain{
				li{
					padding: 7px 15px;
					p{
						text-align: left;
					}
				}
			}
		}
		.tagcloud, .textwidget, #loginform{
			padding: 7px 15px;
		}
		.textwidget{
			h3{
				padding: 13px 0px 9pt 0px;
			}
		}
		select{
			margin: 7px 15px;
		}

	&.banner{
		margin-bottom: 5px;
	}

		#searchform{
			padding-left: 15px;
			padding-right: 15px;
			.checkbox{
				label{
					display: block;
				}
			}
			&.searchform_for_post{
				@include searchform_for(post);
			}
			&.searchform_for_documentos{
				@include searchform_for(documentos);
			}
			&.searchform_for_convocatorias{
				@include searchform_for(convocatorias);
			}
			&.searchform_for_producto{
				@include searchform_for(producto);
			}
		}

	}
}

@media(max-width: $screen-xs-max){
	.sidebar{
		.widget{
			&.banner{
				@include make-xs-column(6);
			}
		}
	}
}
