ul#menu-navegacion-primaria{
	li{
		&.active, &.current-page-ancestor, &.current-menu-ancestor{
			a, a:hover{
				color: $red--google;
				background-color: transparent;
			}
		}

		a{
			@include font-family(opensans, bold);
			text-transform: uppercase;
			color: $brown--lighter;
			font-size: 13px;
			&:hover{
				color: $red--google--hover;
			}
		}

		&.dropdown{
			ul.dropdown-menu{
				@include border-radius(0);
				@media(max-width: $screen-xs-max){
					background-color: $brown--lighter;
				}
				li{
					a{
						@include font-family(opensans, light);
						font-size: 14px;
						text-transform: none;
						color: $gray;
						padding: 5px 20px;
						&:hover{
							background-color: $red--google--light;
						}
					}
					&.sub-item{
						a{
							font-size: 13px;
						}
					}
					&.dropdown-header{
						font-size: 13px;
						text-transform: uppercase;
						@include entry-title(xs);
						color: $brown;
					}
					&.active{
						a, a:hover{
							background-color: $red--google;
							color: $white;
						}
					}
				}

				&:hover{
					li{
						&.dropdown-header{
							color: $red;
						}
					}
				}
			}
			&:hover{

			}
			li{
				&.dropdown{
					a:first-of-type {
						&:before{
							@include fa-icon();
							content : '\f105';
							position: absolute;
							right: 10px;
						}
					}

					ul.dropdown-menu{
						display: none;
					}

					&:hover{
						ul.dropdown-menu{
							display: block;
							top: 0;
							left: 100%;
							right: auto;
							li{

								a{
									&:before{
										display: none;
									}
								}
							}
						}
					}
				}
			}
		}
		&.open > a{
			color: $red--google--hover;
			background-color: $brown--light;
		}
	}
}
.super-header{
	ul{
		li{
			&.active, &.current-page-ancestor, &.current-menu-ancestor{
				a, a:hover{
					color: $yellow;
				}
			}
			&.dropdown{
				ul.dropdown-menu{
					z-index: 1001;
					@include border-radius(0);
					li{
						a{
							@include font-family(opensans, light);
							font-size: 13px;
							text-transform: none;
							color: $gray !important;
							padding: 5px 20px;
							&:hover{
								background-color: $yellow--light;
							}
						}
						&.dropdown-header{
							font-size: 10px;
							text-transform: uppercase;
							@include entry-title(xs);
							color: $gray--light;
						}
						&.active{
							a, a:hover{
								background-color: $red--google;
								color: $white;
							}
						}
					}

					&:hover{
						li{
							&.dropdown-header{
								color: $black;
							}
						}
					}
				}
				&:hover{
					ul.dropdown-menu{
						position: absolute;
						width: 100%;
						background-color: $white;
						li{
							display: block;
						}
					}
				}
			}
			&.menu-icon{
				padding-right: 3px;
				a{
					@include fa-icon();
					width: 34px;
					height: 34px;
					padding: 0;
					text-align: center;
					line-height: 34px;
					@include border-radius(0);
					span{
						@include text-hide();
					}
					&:before{
						color: $white;
					}
				}
			}
			&.pte{
				a{
					font-size: 11px;
					height: auto;
					line-height: 20px;
					padding-top: 5px;
					padding-bottom: 5px;
					background-color: $green--light;
					&:hover{
						background-color: $green--light;
					}
					&:before{
						position: absolute;
						top: 4px;
						left: -10px;
						content: url('../images/pte__icon.png');
					}
					width: auto;
					padding-left: 10px;
					padding-right: 10px;
					span{
						@include font-family(opensans, bold);
						color: $white;
						font-size: 11px;
						padding-left: 5px;
					}
				}
			}
		}
	}
}

ul#menu-social-navegacion{
	@include make-row(0);
	width: 100%;
	margin-right: 0;
	margin-bottom: 30px;
	padding: 0 15px;
	li{
		@include make-sm-column(4,0);
		@include make-xs-column(4,0);
		&.menu-icon{
			a{
				@include fa-icon();
				background-color: $gray;
				width: 100%;
				height: 30px;
				padding: 0;
				text-align: center;
				line-height: 30px;
				@include border-radius(0);
				span{
					@include text-hide();
				}
				&:hover{
					&:before{
						color: $white;
					}
				}
			}
		}
		&.fb{
			a{
				background-color: $white--smoke;
				&:hover{
					color: $white--smoke;
					background-color: $blue--fb !important;
				}
				&:before{
					color: $blue--fb;
					content: $fa-var-facebook;
				}
			}
		}
		&.tw{
			a{
				background-color: $white--smoke;
				&:hover{
					background-color: $blue--tw !important;
				}
				&:before{
					color: $blue--tw;
					content: $fa-var-twitter;
				}
			}
		}
		&.yt{
			a{
				background-color: $white--smoke;
				&:hover{
					background-color: $red--google !important;
				}
				&:before{
					color: $red--google;
					content: $fa-var-youtube;
				}
			}
		}
	}
}

.mega-menu{
	.mega-sub-menu{
		#mega-menu-item-text-3, #mega-menu-item-text-4, #mega-menu-item-text-5, #mega-menu-item-text-6{
			padding: 0 !important;
		}
	}
}

@media (max-width: $screen-xs-max) {
	.navbar-branding{
		.navbar-brand{
			margin: 0 auto;
			width: 100%;
			a{
				margin: 0 auto;
			}
		}
		nav{
			.menu-social-navegacion-container{
				width: 100%;
				text-align: center;
				ul{
					margin: 0 auto;
					&.navbar-right{
						li{
							display: inline;
							float: none !important;
						}
					}
				}
			}
		}
	}
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: $screen-sm-min) {
	ul#menu-navegacion-primaria, ul#menu-super-navegacion-izquierda{
		li.dropdown{
			&:hover{
				ul.dropdown-menu{
					width: auto;
				}
			}
		}
	}
	ul#menu-social-navegacion{
		padding: 0;
	}
}
